<template>
  <div class="app-container">
    <div>
      <ld-header>登记票据明细</ld-header>
    </div>
    <div class="view-container">
      <view-a
        v-if="config"
        :config="config"
        @onInput="onInput"
      ></view-a>
    </div>
    <div>
      <ld-bottom :list="bottom"></ld-bottom>
    </div>
  </div>
</template>

<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ViewA from '../../view/A';
import ld from '../../utils/ld.js';
import service from '../../service';

export default {
  data() {
    const that = this;

    return {
      config: null,
      bottom: [
        {},
        {
          icon: 'confirm',
          onClick() {
            ld.globalData.set(that.storageKey, that.config.tableData);
            ld.navigateBack();
          }
        }
      ]
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    /**
     * 页面取数方法
     */

    async loadData() {
      try {
        let urlParams;
        let fieldConfig; // 字段配置信息
        let viewConfig; // 界面配置信息
        let storageKey; // storage键值名
        let storageData; // storage存储的数据
        let tableData = [{}];

        // 从url上获取参数
        urlParams = ld.getUrlParams();
        // 从url参数获取缓存的键值名
        storageKey = urlParams.storageKey;

        // 全局缓存有数据，则优先从全局缓存获取
        storageData = ld.globalData.get(storageKey);
        if (storageData) {
          tableData = storageData;
        }

        // 获取字段配置信息
        fieldConfig = await service.getMockFieldConfig('/E12/index5');

        // 获取界面配置信息
        viewConfig = await service.getMockViewConfig('/E12/index5');

        // 设置数据信息
        this.config = {
          tableData: tableData,
          fieldConfig: fieldConfig, // 字段信息
          ...viewConfig,
        };
        // 设置storage键
        this.storageKey = storageKey;

      } catch (err) {
        console.log(err);
      }
    },

    /**
     * 监听输入事件，合计值动态计算
     */

    onInput(item, rowIdx, colIdx) {
      let sumField; // 合计字段

      // TODO 还没想好如何动态计算合并字段
      let zhangshu; // 张数
      let danjia; // 单价

      sumField = this.config.sumField;
      zhangshu = Number(item.zhangshu) || 0; // 强制转换成数字，若转换不了，则设置为0
      danjia = Number(item.danjia) || 0;

      // 合计运算
      this.config.tableData[rowIdx][sumField] = zhangshu * danjia;
    }
  },
  components: {
    'ld-header': Header,
    'ld-bottom': Bottom,
    'view-a': ViewA,
  },
};
</script>

<style scoped>
.view-container {
  padding-left: 0;
  padding-right: 0;
}
</style>



