    viewType: 'A',
    // 输入参数列表
    queryList: ['PName'], // 票据名：根据票据名查询字段
    nextPageQuery: 'piaoju=piaoju',
    getId: '', // 取数ID
    // 识别标识
    identifyValue: 'view4', // 跳转的表格界面ID
    jianzhiming: 'B', // 键值名
    // fieldList: ['piaoju', 'zhangshu', 'jine'], // 显示字段列表
