const addData = {
  'view3': [
    {
      id: '0001',
      piaoju: '过路过桥费',
    },
    {
      id: '0002',
      piaoju: '燃油销售发票',
    },
    {
      id: '0003',
      piaoju: '对应票据',
    },
  ]
};

export default {
  get(id) {
    return store[id];
  },

  getAddData(id) {
    return addData[id];
  },

  getFieldConfig(id) {
    return fieldConfig[id];
  },

  getViewConfig(id) {
    return viewConfig[id];
  }
};



