import router from '../router';
import { VIEW_TYPE_A, VIEW_TYPE_B, CHILD_DATA_VALUE_KEY, CHILD_DATA_INDEX_KEY, CHILD_DATA_KEY_MAP } from './const';

/**
 * url参数转成对象
 *
 * @param {string} urlParams url参数
 */

function urlParamsToObject(urlParams) {
  const obj = {};
  const arr = urlParams.split('&');

  arr.forEach(str => {
    const item = str.split('=');
    obj[item[0]] = item[1];
  });

  return obj;
}

/**
 * 全局变量存储存储方法，可在页面之间传递
 */
const globalData = {
  /**
   * 设置全局变量
   *
   * @param {string} k 键
   * @param {string | Object} v 存储的值
   */

  set(k, v) {
    try {
      if (typeof v === 'object') {
        v = JSON.stringify(v);
      }
    } catch (err) {}
    sessionStorage.setItem(k, v);
  },

  /**
   * 获取全局变量
   *
   * @param {string} k 键
   * @return {string | Object} 值
   */

  get(k) {
    let v = sessionStorage.getItem(k);
    try {
      v = JSON.parse(v);
    } catch (err) {}
    return v;
  },

  /**
   * 获取全部全局变量
   */

  getAll() {
    const result = {};
    Object.keys(sessionStorage).forEach(key => {
      result[key] = this.get(key);
    });
    return result;
  }
};

export default {

  /**
   * 获取所有子页面storage键
   */

  getChildKeyMap() {
    return globalData.get(CHILD_DATA_KEY_MAP) || {};
  },

  /**
   * 记录子页面storage键，存到一个hash表里
   * {
   *   行号1: storage键1
   *   行号2: storage键2
   * }
   *
   * @param {string} key 子页面storage键
   * @param {number} rowIdx 行号
   */

  recordChildKey(key, rowIdx) {
    let keyMap;
    keyMap = globalData.get(CHILD_DATA_KEY_MAP);

    // 若全局中从没有记录过，新建一个数组存入
    if (!keyMap || typeof keyMap !== 'object') {
      keyMap = {};
      keyMap[rowIdx] = key;
      globalData.set(CHILD_DATA_KEY_MAP, keyMap);
      return;
    }

    // 当前键值在列表中没有才记录
    if (!keyMap[rowIdx]) {
      keyMap[rowIdx] = key;
      globalData.set(CHILD_DATA_KEY_MAP, keyMap);
      return;
    }
  },

  /**
   * 清除全局子页面属性
   */

  clearChilrenData() {
    globalData.set(CHILD_DATA_VALUE_KEY, '');
    globalData.set(CHILD_DATA_INDEX_KEY, '');
  },

  /**
   * 将数据列表的某一个字段求和
   *
   * @param {Array<Object>} arr 数据列表
   * @param {*} key 字段名
   * @return {number} 求和的值
   */
  sumWithKey(arr, key) {
    if (!Array.isArray(arr)) {
      return 0;
    }

    return arr.reduce((pre, cur) => {
      return pre + (Number(cur[key]) || 0);
    }, 0);
  },

  // 存取全局变量方法
  globalData,

  /**
   * 根据传入的id跳转界面
   *
   * @param {Object} config 界面配置
   * @param {string} config.nextViewId 下级界面编码id
   * @param {Array} config.nextPageQuery 下级界面参数列表
   * @param {Object} item 点击的元素数据
   * @param {number} rowIdx 行号（从0开始算）
   */
  navigateTo(config, item = {}, rowIdx) {
    if (typeof config === 'string') {
      // 跳转普通界面
      router.push({ path: config, query: item || {} });
    }
    // A界面从identifyValue属性取，B界面从nextViewId取
    let viewId = null;
    if (config.viewType === VIEW_TYPE_A) {
      viewId = config.identifyValue;
    } else if (config.viewType === VIEW_TYPE_B) {
      viewId = config.nextViewId;
    } else {
      return;
    }

    const extraQuery = {}; // 根据配置，需要从本界面传递的参数

    if (config.nextPageQuery) {
      const state = config.state;
      // url参数从这里获取，里面包括上一个界面传过来的数据，以及点击的行带有的数据
      const bigData = {
        ...state,
        ...item,
      };

      const nextPageQuery = config.nextPageQuery; // 传给下一个页面的参数对应关系
      const obj = urlParamsToObject(nextPageQuery); // 将url参数转成对象

      Object.keys(obj).forEach(k => {
        const stateParam = obj[k]; // 需要获取的当前界面属性名
        extraQuery[k] = bigData[stateParam]; // 根据属性名获取对应的值
      });
    }

    router.push({ query: { viewId: viewId, ...extraQuery, keyId: `${config.jianzhiming}_${rowIdx}` }});

    // 跳转前清除子页面数据
    this.clearChilrenData();
  },

  /**
   * 返回上一个页面
   */
  navigateBack() {
    router.go(-1);
  },

  /**
   * 获取url参数
   *
   * @return {Object} url参数，对象结构
   */
  getUrlParams() {
    return router.currentRoute.query;
  },

  /**
   * 设置网页标题
   * @param {string} title 副标题
   */
  initTitle(subTitle) {
    const prefix = '联道OA';
    document.title = prefix + (subTitle ? `\\${subTitle}` : '');
  },

  /**
   * 等待指定时间后再执行
   *
   * @param {number} time 等待时间
   */
  sleep(time = 200) {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve();
      }, time);
    });
  }
};



